import React from 'react'
import { Card as CardComponent } from 'theme-ui'
import { Layout, Stack, Main, Sidebar } from '@layout'
import CardList from '@components/CardList'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import Sticky from '@components/Sticky'
import AuthorCompact from '@widgets/AuthorCompact'
import TableOfContentsCompact from '@widgets/TableOfContentsCompact'
import {
  PostHead,
  PostImage,
  PostBody,
  PostComments,
  PostCommentsFacebook,
  PostCommentsGraph,
  PostTagsShare
} from '@widgets/Post'
import { MDXProvider } from '@mdx-js/react' 
import { Bokeh, MathJax}  from './Extensions'

const mdxcomponents = {
  Bokeh, MathJax
}

const Post = ({
  data: { post, tagCategoryPosts, tagPosts, categoryPosts, previous, next },
  ...props
}) => {
  const relatedPosts = [
    ...(tagCategoryPosts ? tagCategoryPosts.nodes : []),
    ...(tagPosts ? tagPosts.nodes : []),
    ...(categoryPosts ? categoryPosts.nodes : [])
  ]
  const { pageContext: { services = {}, siteUrl } = {} } = props

  console.log("AUTHOR:", post.author)
  console.log("CATEGORY:", post.category)
  console.log("TOC:", post.tableOfContents?.items)

  return (
    <MDXProvider components={mdxcomponents}>
    <Layout {...props}>
      <Seo {...post} siteUrl={siteUrl} />
      <Divider />
      <Stack effectProps={{ effect: 'fadeInDown' }}>
        <PostHead {...post} />
      </Stack>
      <Divider space={3} />
      <Stack effectProps={{ fraction: 0 }}>
        <Main>
          <CardComponent variant='paper'>
            <PostImage {...post} inCard />
            <PostBody {...post} />
            <PostTagsShare {...post} location={props.location} />
            {services.disqus && <PostComments {...post} />}
            {services.graphComment && <PostCommentsGraph {...post} />}
            {services.facebookComment && (
              <PostCommentsFacebook {...post} siteUrl={siteUrl} />
            )}
          </CardComponent>
          <Divider />
        </Main>

        {(post.author || post.tableOfContents?.items || post.category) && (
          <Sidebar>
            <AuthorCompact author={post.author} omitTitle />
            <Divider />
            <Sticky>
              {post.tableOfContents?.items && (
                <>
                  <TableOfContentsCompact {...post} />
                  <Divider />
                </>
              )}
              {post.category && (
                <CardList
                  title='Related Posts'
                  nodes={relatedPosts}
                  variant='horizontal-aside'
                  limit={6}
                  omitMedia
                  omitCategory
                  distinct
                  aside
                />
              )}
            </Sticky>
          </Sidebar>
        )}


      </Stack>
    </Layout>
    </MDXProvider>
  )
}

export default Post